import React from 'react';
import {Text, View} from 'react-native';
import EStyleSheet from 'react-native-extended-stylesheet';
import debugColors from './debugColor';

export default class NavHeader extends React.Component {
  render() {
    return (
      <View style={styles.header}>
        <View style={styles.headerLeft}>
          <Text style={styles.nav}>PREVIOUS WORK</Text>
          <Text style={styles.nav}>PROJECTS</Text>
        </View>
        <View style={styles.headerCenter}>
          <Text style={styles.name}>GABRIEL GRUTER</Text>
        </View>
        <View style={styles.headerRight}>
          <Text style={styles.nav}>CV</Text>
          <Text style={styles.nav}>CONTACT ME</Text>
        </View>
      </View>
    );
  }
}

const styles = EStyleSheet.create ({
  header: {
    zIndex: 2,
    height: '5vh',
    flexDirection: 'row',
    justifyContent: 'center',
  },
  headerLeft: {
    width: '25%',
    flexDirection: 'row',
    alignItems: 'center',
  },
  headerCenter: {
    width: '50%',
    justifyContent: 'flex-end',
    alignItems: 'center',
    paddingTop: '4.5vw',
  },
  headerRight: {
    width: '25%',
    justifyContent: 'flex-end',
    alignItems: 'center',
    flexDirection: 'row',
  },
  nav: {
    color: '$black',
    fontFamily: 'Roboto',
    marginLeft: '10%',
    marginRight: '10%',
    fontSize: '1vw',
  },
  name: {
    color: '$black',
    fontFamily: '$serifFont',
    fontSize: '3.5vw',
  }
});