import React from 'react';
import {Text, View} from 'react-native';
import EStyleSheet from 'react-native-extended-stylesheet';
import debugColors from './debugColor';
import ScrollDown from './scrollDown';

export default class BackgroundRules extends React.Component {
  render() {
    return (
      <View style={styles.container}>
        <View style={styles.ruleBox}/>
        <View style={styles.ruleBox}>
          <View style={styles.rule}/>
        </View>
        <View style={styles.ruleBox}>
          <View style={styles.rule}/>
        </View>
        <View style={styles.ruleBox}>
          <View style={styles.rule}/>
        </View>
        <View style={styles.ruleBox}>
          <View style={styles.rule}/>
        </View>
        <View style={styles.ruleBox}>
          <View style={styles.rule}/>
        </View>
        <View style={styles.ruleBox}>
          <View style={styles.rule}/>
          <ScrollDown/>
        </View>
        <View style={styles.ruleBox}>
          <View style={styles.rule}/>
        </View>
      </View>
    );
  }
}

const styles = EStyleSheet.create ({
  container: {
    position: 'absolute',
    flexDirection: 'row',
    height: '100%',
    width: '100%',
  },
  ruleBox: {
    flexDirection: 'row',
    width: '12.5vw',
    height: '100%'
  },
  rule: {
    width: '0.1vw',
    height: '100%',
    backgroundColor: '$black',
    opacity: 0.1
  }
});