import React from 'react';
import {Text, View} from 'react-native';
import EStyleSheet from 'react-native-extended-stylesheet';
import debugColors from './debugColor';

export default class ScrollDown extends React.Component {
  render() {
    return (
      <View style={styles.scrollBox}>
        <Text style={styles.scrollText}>SCROLL DOWN</Text>
        <View style={styles.verticalRule}/>
      </View>
    );
  }
}

const styles = EStyleSheet.create ({
  $textRotation: 'rotate(90deg)',
  scrollBox: {
    // backgroundColor: '$debug1',
    height: '100vh',
    width: '199%',
    justifyContent: 'flex-end',
    alignItems: 'center'
  },
  scrollText: {
    transform: '$textRotation',
    webkitTransform: '$textRotation',
    mozTransform: '$textRotation',
    msTransform: '$textRotation',
    oTransform: '$textRotation',
    position: 'absolute',
    bottom: '5.5vw',
    textAlign: 'center',
    fontFamily: 'Roboto',
    fontStyle: 'italic',
    color: '$black',
    fontSize: '1vw',
  },
  verticalRule: {
    width: '0.1vw',
    height: '2.5vw',
    backgroundColor: '$black',
    marginTop: '3vw',
  }
});